@use '../base/variables' as v;
@use '../base/mixins' as m;

.lineup {
    background-color: v.$rojo;
    h3, .dia {
        text-align: center;
        color: v.$blanco;
    }
    .dia {
        font-size: 3rem;
        text-transform: uppercase;
    }
}

.escenarios-contenedor {
    @include m.contenedor;
    @include m.tablet {
        @include m.grid(2);
    }
}

.escenario {
    padding-top: 2rem;
    .nombre-escenario {
        font-size: 3rem;
        font-weight: 900;
        text-transform: uppercase;
        text-align: center;
    }
}

.calendario {
    @include m.resetear-lista;

    li {
        padding: 2.5rem;
        font-weight: 700;
        span {
            font-weight: 400;
        }
    }
}

.bg-azulcielo {
    background-color: v.$azulcielo;
    li:nth-child(odd) {
        background-color: lighten(v.$azulcielo, 5%);
    }
}

.bg-azulmedio {
    background-color: v.$azulmedio;
    li:nth-child(odd) {
        background-color: lighten(v.$azulmedio, 5%);
    }
}